<template>
    <div id="WatchComputed">
        <input type="text" v-model="TXTone">
        <input type="text" v-model="txttow">
        <input type="text" v-model="TXTtheer">
    </div>
</template>

<script>
/*  实现效果：字符串的动态拼接实现
    1.methods方法   2.watch监听方法     3.computed计算属性实现
*/

export default {
    
    data() {
        return {
            TXTone:'',
            txttow:'',
        }
    },
    /*1  methods: {
         methods方法   @keyup="add"    @keyup="add"
        add(){
            this.TXTtherr=this.TXTone+'+'+this.txttow
        }
    },             */

    //   watch: {
    //     TXTone:function(xin,jiu){
    //         this.TXTtherr=xin+'+'+this.txttow
    //     },
    //     txttow:function(x,j){
    //          this.TXTtherr=this.TXTone+'+'+x
    //     }
    // },          
    computed: {
        TXTtheer:function(){
            return this.TXTtherr=this.TXTone+'+'+this.txttow
        }
    },
}
</script>

<style lang="scss">
    
</style>


